import { Input, Button, message, Progress } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import './uploader.css';
import { SetStateAction, useEffect, useState } from 'react';
// for web3 storage
// import { Web3Storage } from 'web3.storage';
import { Web3Storage } from 'web3.storage/dist/bundle.esm.min.js';

// 浏览文件以及上传按钮组合组件
function InputFile(props: any) {
	const [uploading, setUploading] = useState<boolean>(false);

	const handleUpload = async () => {
		setUploading(true);

		const client = new Web3Storage({
			token: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiJkaWQ6ZXRocjoweDQ0RTFDMzZkREM5NjI2MjY1ZWM5OWJhNkUwYzZmZDE0Y0I3MjA2NkUiLCJpc3MiOiJ3ZWIzLXN0b3JhZ2UiLCJpYXQiOjE2MzU1NzM4NzIyNTYsIm5hbWUiOiJ1cGxvYWRlciJ9.qJ4Q_9IaazEYoy9qey5dCVCGw7O315kbbODobUnBW1I',
		});

		const onRootCidReady = (cid: any) => {
			console.log('uploading files with cid:', cid);
			//修改cid
			props.changeCid(cid);
		};

		const totalSize = 1;
		let uploaded = 0;

		const onStoredChunk = (size: number) => {
			uploaded += 1;
			const pct = (uploaded / (totalSize as number)) * 100;
			console.log(`Uploading... ${pct.toFixed(2)}% complete`);
			if (uploaded >= (totalSize as number)) {
				setUploading(false);
				message.success('uploaded!');
			}
			props.changePercent(parseInt(pct.toFixed()));
		};

		return await client.put(makeFileObjects(), {
			name: encodeURIComponent('team'),
			wrapWithDirectory: false,
			maxRetries: 3,
			onRootCidReady,
			onStoredChunk,
		});
	};

	function makeFileObjects(): File[] {
		// const obj = { hello: 'world' }
		// const blob = new Blob([JSON.stringify(obj)], { type: 'application/json' })
	  
		let multilineText = (document.getElementById('teamInput') as HTMLInputElement)?.value;
		if (multilineText === undefined) {
			multilineText = "empty" 
		}
		const blob = new Blob([multilineText], { type: 'text/plain' });
		const files = [
		  new File(['contents-of-file-1'], 'plain-utf8.txt'),
		  new File([blob], 'hello.json')
		]
		return files
	  }

	return (
		<div className="inputFile1">
			<div className="input1" style={{ width: '65%' }}>
				<Input.TextArea id="teamInput"
					placeholder="6 batsman, 6 bowlers, 2 max sixes"
					rows={14}
				/>
			</div>
			<div className="button1" style={{ width: '120px' }}>
				<Button
					icon={<UploadOutlined />}
					type="primary"
					onClick={handleUpload}
					loading={uploading}
					size="large"
					style={{ width: '120px' }}
					>
					{uploading ? 'Uploading' : 'Upload'}
				</Button>
			</div>
		</div>
	);
}

// 上传后文显示组件
function TextTip(props: any) {
	const [cid, setCid] = useState('');
	const [url1, setUrl1] = useState('');

	useEffect(() => {
		setUrl1(cid === '' ? '' : 'https://ipfs.io/ipfs/' + cid);
	}, [cid]);

	useEffect(() => {
		setCid(props.cid);
	}, [props.cid]);

	return (
		<div id="textTip-id">
			<p>
				Share this hash now:
				<br />
				{cid.slice(-10)}
			</p>
			<p>
				Your team has been published here. Pls save the url:
				<br />
				<a href={'https://ipfs.io/ipfs/' + cid} target="_blank" rel="noreferrer" white-space="nowrap">
					{url1}
				</a>
			</p>
		</div>
	);
}

// uploader总组件
// 标题
// 按钮
// 文字显示
function Uploader() {
	const [cid, setCid] = useState('');
	const [percent, setPercent] = useState(0);

	return (
		<div className="uploader-div">
			<div className="title">
				<h1>Publish your team here</h1>
			</div>
			<div className="inputFile-div">
				<InputFile
					changeCid={(cid: SetStateAction<string>) => {
						setCid(cid);
					}}
					changePercent={(percent: SetStateAction<number>) => {
						setPercent(percent);
					}}
				/>
			</div>
			<div className="progress">
				<Progress percent={percent} status="normal" showInfo={true} style={{ width: '46%', left: '-1%' }} />
			</div>
			<div className="textTip-div">
				<TextTip cid={cid} />
			</div>
		</div>
	);
}

export default Uploader;
