import React from 'react';
import './header.css';

function MyHeader() {
    return (
        <div className="header-div">
            <div className="header-img">
                <img src={'./imgs/ipl.jpg'} alt="logo" />
            </div>
            <div className="header-title">CBL 2023</div>
        </div>
    );
}

export default MyHeader;
